import React from 'react';
import { useTranslation } from 'react-i18next';
import { shallowEqual } from 'react-redux';
import MiniBox from '../../../layout/MiniBox';
import Status from '../../../components/Status';
import { useAppSelector } from '../../../hooks/store';
import { eventEmitter, Events } from '../../../utils/events';
import { useDrawer } from '../../../hooks/drawer';

const CardDetails = React.lazy(() => import('../CardDetails/CardDetails'));

function CardActivated() {
  const { t } = useTranslation();
  const { open } = useDrawer();

  const card = useAppSelector((store) => store.card.selectedCard, shallowEqual);
  return (
    <div className="page">
      <MiniBox>
        <Status
          status="success"
          title={t('activate_card_success.title')}
          ctaText={t('activate_card_success.ctaText')}
          description={t('activate_card_success.description')}
          onNext={() => {
            if (card) {
              eventEmitter.dispatch(Events.CARD_REFRESH, {});
            }
            open(CardDetails);
          }}
        />
      </MiniBox>
    </div>
  );
}

export default CardActivated;
